.hero {
  height: 1000px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

.people {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.people::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.people * {
  position: relative;
  z-index: 2;
}

.people h1, .people h2 {
  color: white;
}

.people h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

.delete-btn {
  float: right;
  color: #e74944;
}

.card {
  background-size: cover;
  background-position: center;
  background-clip: content-box;
  border: 6px solid white;
  height: 400px;
  width: 400px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  position: relative;
  margin: 0 auto;
  margin-top: 30px;
}

.card .fa-spinner {
  font-size: 180px;
  position: absolute;
  left: 25%;
  top: 25%;
}

.footer {
  min-height: 50px;
  background: #8EFFF9 ;
  font-size: 40px;
  position: relative;
}


.navbar-default {
  margin-bottom: 0;
}
.footer {
  min-height: 50px;
  background: #8EFFF9 ;
  font-size: 40px;
  position: relative;
}


.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
}

* {
  box-sizing: border-box;
  font-family: Avenir;
  color: #0433FF;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

